import { CreditCard, CreditScore, DateRange, Person, Pix, QrCode } from "@mui/icons-material";
import { Accordion, AccordionDetails, AccordionSummary, Button, FormControl, FormControlLabel, FormLabel, Grid, InputAdornment, Radio, RadioGroup, TextField, Typography } from "@mui/material";
import { Box } from "@mui/system";
import { useEffect } from "react";

export default function Payment({ back, handleChangeProgress, handleCreateCompany }) {

    function handlePay() {
        handleCreateCompany();
    };

    useEffect(() => {
        handleChangeProgress(95)
    }, [])

    return (
        <Grid container spacing={1}>
            <Grid item xs={12}>
                <Typography sx={{ mt: '1em' }} variant='h2'>Pagamento</Typography>
                <Accordion>
                    <AccordionSummary>
                        <CreditCard color={"primary"} />
                        <Typography fontWeight={700} color={"primary"} ml=".25em">
                            Carto de Crdito
                        </Typography>
                    </AccordionSummary>
                    <AccordionDetails>
                        <Grid container spacing={1}>
                            <Grid item xs={12} sm={6}>
                                <TextField InputProps={{
                                    startAdornment: (
                                        <InputAdornment position="end">
                                            <Person />
                                        </InputAdornment>
                                    ),
                                }} fullWidth placeholder="Nome do Titular" />
                            </Grid>
                            <Grid item xs={12} sm={6}>
                                <TextField InputProps={{
                                    startAdornment: (
                                        <InputAdornment position="end">
                                            <CreditCard />
                                        </InputAdornment>
                                    ),
                                }} fullWidth placeholder="Nmero do Carto" />
                            </Grid>
                            <Grid item xs={6} sm={3}>
                                <TextField InputProps={{
                                    startAdornment: (
                                        <InputAdornment position="end">
                                            <DateRange />
                                        </InputAdornment>
                                    ),
                                }} fullWidth placeholder="Validade" />
                            </Grid>
                            <Grid item xs={6} sm={3}>
                                <TextField InputProps={{
                                    startAdornment: (
                                        <InputAdornment position="end">
                                            <CreditScore />
                                        </InputAdornment>
                                    ),
                                }} fullWidth placeholder="CVV" />
                            </Grid>
                            <Grid item xs={12} sm={6}>
                                <FormControl>
                                    <FormLabel id="demo-form-control-label-placement">Forma de pagamento</FormLabel>
                                    <RadioGroup
                                        row
                                        aria-labelledby="demo-form-control-label-placement"
                                        defaultValue="top"
                                    >
                                        <FormControlLabel
                                            value={0}
                                            control={<Radio />}
                                            label={<Typography color={'grey'}>Dbito (1X)</Typography>}
                                            labelPlacement="end"
                                        />
                                        <FormControlLabel
                                            value={1}
                                            control={<Radio />}
                                            label={<Typography color={'grey'}>Crdito (1X)</Typography>}
                                            labelPlacement="end"
                                        />
                                        <FormControlLabel
                                            value={2}
                                            control={<Radio />}
                                            label={<Typography color={'grey'}>Crdito (12X)</Typography>}
                                            labelPlacement="end"
                                        />

                                    </RadioGroup>
                                </FormControl>
                            </Grid>
                        </Grid>
                    </AccordionDetails>
                </Accordion>
                <Accordion>
                    <AccordionSummary>
                        <QrCode color={"primary"} />
                        <Typography fontWeight={700} color={"primary"} ml=".25em">
                            Boleto
                        </Typography>
                    </AccordionSummary>
                    <AccordionDetails>
                        <Button fullWidth size="large" variant="contained" startIcon={<QrCode />} color="success">
                            Emitir boleto
                        </Button>
                    </AccordionDetails>
                </Accordion>
                <Accordion>
                    <AccordionSummary>
                        <Pix color={"primary"} />
                        <Typography fontWeight={700} color={"primary"} ml=".25em">
                            PIX
                        </Typography>
                    </AccordionSummary>
                    <AccordionDetails>
                        <Button fullWidth size="large" variant="contained" startIcon={<Pix />} color="success">
                            Pagar agora
                        </Button>
                    </AccordionDetails>
                </Accordion>
            </Grid>
            <Grid item xs={12} sx={{
                display: 'flex',
                justifyContent: 'space-between'
            }}>
                <Button onClick={() => { back() }}>
                    Voltar
                </Button>
                <Button size="large" variant="contained" onClick={() => { handlePay() }}>
                    Divulgar vagas agora!
                </Button>
            </Grid>
        </Grid>
    )
}